.body{
    margin-left:5%;
    margin-top:120px;
    font-family: 'Roboto', sans-serif;
}

.header{
    font-size:47px;
    font-weight: bold;
    margin-bottom:5px;
    display: block;
    margin-left: 1%;
}

.description{
    color:#868E96;
    margin-bottom:22px;
    display: block;
    font-size:20px;
    margin-left: 1%;
}

.cart{
    display: inline-block;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-left:0.5%;
    height:200px;
    width: 435px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-right:1.3%;
    border-radius: 5px;
}

.cart_description{
    width: 370px;
    display: inline-block;
}

.cart:hover{
    background: red;
    color:white;
    transition: 1s;
}

.title{
    font-weight: 700;
    font-size:22px;
    display: inline-block;
    margin-bottom:17px;
}

.cart_content{
    padding-left:20px;
    display: block;
}

.strelka{
    background: red;
    border: none;
    color:white;
    padding-top:7px;
    padding-bottom: 7px;
    padding-left:11px;
    font-size:17px;
    border-radius: 0px 0px 0px 50px;
    display: inline;
    margin-left: 92%;
}
